import React from 'react'

const Container = ( props ) => {
  return (
    <div className={`p-8 md:p-16 w-screen h-screen transition-colors duration-1000 ease-expo ${props.darkTheme === false ? "bg-A2A2A4" : "bg-0B0B0B"}`}> 
      <div className={`relative flex flex-col justify-center items-center w-full h-full rounded-mobile md:rounded transition-colors duration-1000 ease-expo ${props.darkTheme === false ? "bg-C3C4C7" : "bg-131313"}`}>
        {props.children}
      </div>
    </div>
  )
}
 
export default Container