import React, { useEffect } from 'react'
import PropTypes from "prop-types"
 
const Title = ( props ) => {

  // Animations
  useEffect(() => {
    // Entering

    //When component is unmounted
    return () => {
      // Leaving
    }
  }, [])
  
  return (
    <div className="text-center text-white text-small md:text-normal textShadowNormal">
      <h2 className="block w-full">
        {props.name}
      </h2>
    </div>
  )
}

Title.propTypes = {
  name: PropTypes.string
}
 
export default Title