import React from 'react'
import PropTypes from 'prop-types'

const Product = (props) => {
  return (
    <div className="flex w-full h-full">
      <img 
        className="w-auto h-full" 
        src={props.src} 
        alt=""
      />
      <div className={`flex-1 flex flex-col items-start justify-between h-full ${props.supertitle ? "pl-16" : "pl-10"} ${props.darkmode === true ? "text-white" : "text-black"}`}>
        <div>
          {props.supertitle &&
            <span className="block mb-4 leading-none text-small">
              {props.supertitle}
            </span>
          }
          <h3 className="leading-none text-normal">{props.title}</h3>
          {props.description &&
            <p className="mt-24 text-small">{props.description}</p>
          }
        </div>
        <div>
          <p className={`leading-none ${props.statsDescription ? "text-small" : "text-normal"}`}>
            {props.percentage}{props.statsDescription}
          </p>
        {
          props.cta &&
          <span className="leading-none uppercase text-small">{props.cta}</span>
        }
        </div>
      </div>
    </div>
  )
}

Product.propTypes = {
  darkMode: PropTypes.bool,
  src: PropTypes.string,
  supertitle: PropTypes.string,
  title: PropTypes.string,
  description: PropTypes.string,
  statsDescription: PropTypes.string,
  cta: PropTypes.string
}
 
export default Product