import React from 'react'
import { Link } from 'react-router-dom'
import PropTypes from "prop-types"

const ButtonLink = ( props, { handleButtonClick } ) => {


  // Controls slider
  function handleId(id) {
    if (props.sliderButtons === true)
    switch (id) {
      case "1": return "swiper-button-prev-unique"
      case "3": return "swiper-button-next-unique"
      default: return ""
    }
  }

  return (
    <button 
      className={`relative text-transparent uppercase select-none group text-tiny md:text-small focus:outline-none ${handleId(props.id)}`}
      onClick={props.handleButtonClick}
      >
      {props.name}
      <span className="absolute top-0 left-0 z-10 -m-1 text-white border-white group-hover:border-b-3">
        {props.name}
      </span>
        <span className="absolute top-0 left-0 text-black border-black group-hover:border-b-3">
          {props.name}
        </span>
    </button>
  )
}

ButtonLink.propTypes = {
  name: PropTypes.string
}
 
export default ButtonLink