import React, { useEffect, useRef } from 'react'
import Container from '../components/Container'
import ButtonLink from '../components/ButtonLink'
import Video from '../components/Video'
// import { TweenMax, TimelineLite } from "gsap"
import dataMultimedia from '../data/dataMultimedia.json'
import placeholder from '../resources/placeholder.jpg'

import Swiper from "swiper"
import "swiper/css/swiper.min.css"
import "../swiperCustom.css"
import "../"

function Multimedia( props ) {

  // Handle button click
  function handleButtonClick(button){
    console.log("click", button)
    if (button === "Home") {
      //do something
    }
  }  

  const swiper = useRef(null)
  useEffect(() => {
    swiper.current = new Swiper(".swiper-container", {
      slidesPerView: 'auto',
      grabCursor: true,
      centeredSlides: true,
      navigation: {
        nextEl: '.swiper-button-next-unique',
        prevEl: '.swiper-button-prev-unique'
      }
    })
    // Params
  }, [])  

  return (
    <Container darkTheme={false}>
      <div 
        data-estoquecoÃ±oes="Top content"
        className="w-full h-32 flex-0" 
      />

      <div 
        data-estoquecoÃ±oes="Main content"
        // className="z-10 flex flex-row items-center justify-start flex-1 w-full h-32 overflow-x-scroll"
        className="z-10 flex-1 w-full h-32"
      >
        <div className="swiper-container">
          <div className="swiper-wrapper">
            {dataMultimedia.items.map((item) => {
              return (
                <div 
                  key={item.id} 
                  className="flex items-center justify-center px-16 swiper-slide"
                  >
                  <img className="w-full h-auto" src={placeholder} alt=""/>
                </div>
              )
            })}
          </div>
        </div>
      </div>
      
      <div 
        data-estoquecoÃ±oes="Bottom content"
        className="flex items-center justify-center w-full h-32 px-16 flex-0"
        >
        {dataMultimedia.buttons.map((item) => {
          return (
            <div 
              key={item.id} 
              className="w-1/3 text-center"
            >
              <ButtonLink
                id={item.id}
                name={item.name}
                href={item.href}
                sliderButtons={true}
                handleButtonClick={() => handleButtonClick(item.name)}
              />
            </div>
          )
        })}
      </div>
      </Container>
  )
}

export default Multimedia